export class SortGeo {

    private name: string;
    private field: string;
    private lat: number;
    private lon: number;

    constructor(field: string, lat: number, lon: number, name: string = '_geo_distance') {
        this.name = name;
        this.field = field;
        this.lat = lat;
        this.lon = lon;
    }

    public static make(field: string, lat: number, lon: number, name: string = '_geo_distance'): SortGeo {
        return new SortGeo(field, lat, lon, name);
    }

    public toJSON() {

        let obj: any = {};
        obj[this.name] = {};
        obj[this.name][this.field] = { lat: this.lat, lon: this.lon };
        obj[this.name]['order'] = "asc";
        obj[this.name]['unit'] = "m";

        return obj;
    }
}